/* ############# STYLES TO MAX WIDTH OF 63.99 REM ################# */

@media (max-width: 63.99em) {

    .site-header {background-color: #252f3e; height: 3.7rem;}
        .usa-logo {
            -webkit-box-flex: 1;
            -ms-flex: 1 0 0px;
            flex: 1 0 0;
            font-size: 1.3rem;
            line-height: .9;
            margin: 1.3rem 0 0 0.2rem;
            white-space: nowrap;
        }

        .leaf-logo img {width: 48px; margin: 0.4rem 0.3rem 0 0.2rem;}

}